.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader {
  height: 200px;
  width: 200px;
  box-sizing: border-box;
  border-bottom: 3px solid #ff3d00;
  animation: drawLine 4s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 16px;
  height: 100px;
  background-image: linear-gradient(#ff3d00 6px, rgba(0, 0, 0, 0) 0), linear-gradient(45deg, rgba(0, 0, 0, .02) 49%, #fff 51%), linear-gradient(315deg, rgba(0, 0, 0, .02) 49%, #fff 51%), linear-gradient(#fff 10%, #ff3d00 10%, #ff3d00 90%, #fff 90%);
  background-position: bottom, 0 88px, right 88px, 0 0;
  background-repeat: no-repeat;
  background-size: 3px 3px, 8px 8px, 8px 8px, 16px 88px;
  border-radius: 20px 20px 50px 50px;
  animation: pencilRot 4s linear infinite;
  position: absolute;
  bottom: -6px;
  left: calc(100% + 14px);
  transform: rotate(25deg);
}

@keyframes drawLine {
  0%, 100% {
    width: 0;
  }

  45%, 55% {
    width: 200px;
  }
}

@keyframes pencilRot {
  0%, 45% {
    bottom: -6px;
    left: calc(100% + 14px);
    transform: rotate(25deg);
  }

  55%, 100% {
    bottom: -12px;
    left: calc(100% + 16px);
    transform: rotate(220deg);
  }
}

.container {
  display: flex;
}

img {
  height: auto;
  display: block;
}

.breed-select {
  width: 400px;
}

.cat-info {
  max-width: 1100px;
  background-color: bisque;
  align-items: center;
}

.img-cat {
  width: 400px;
}

.box-cat {
  padding: 20px;
}

